/* Header.css */
.header {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  background-color:#FFFFFF;
  padding: 1px 15px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Added px units */
  display: flex; /* Added for proper flexbox layout */
  flex-direction: column; /* Stack top and bottom sections vertically */
  width: 98%;
  height: 120px;
  
}


.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px; /* Adjusted margin */
}


.header-title {
  font-family: 'Pangram', sans-serif; 
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #5A5A5A;
}


.icons {
  display: flex;
  align-items: center;
}

.icon {
  font-size: 20px; /* Adjusted icon size */
  margin-left: 10px;
  color: #828282; /* Added icon color */
}

.header-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.nav-links {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
}

.nav-item {
  margin-right: 25px; /* Adjusted margin */
  font-family: 'Pangram';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #828282;
  cursor: pointer;
}


.nav-item.active {
  color: #2F80ED;
  text-decoration: underline;
  text-underline-offset: 8px;
}


.grouped-icons {
  display: flex;
  align-items: center;
}


.ellipse-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px; 
  height: 25px; 
  border: 4px dotted gray; 
  border-radius: 50%; 
  margin-right: -60px;
  font-size: 35px;
}


.Pluss-icon {
  justify-content: center;
  font-size: 38px;
  margin-right: -1px;
  color: blue;
  align-items: center;
  margin-top: -11px;
}
.Pluss-icon:hover{
  cursor: pointer;
  font-size: 53;
  color: aqua;
}

.avatar-icons {
  display: flex;
}

.avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-size: cover; 
  margin-left: -50px;
  margin-right: 35px;
}

