
.sidebar {
  background: #FFFFFF;
  box-shadow: 10px 0px 20px rgba(0, 0, 0, 0.1);
  width: 300px; 
  height: 100vh;
  display: flex; 
  flex-direction: row; 
  grid-column: 1/2; 
  grid-row: 1/3;    
  /* padding: 30px;  */
}
.fixed-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0;
  min-width: 60px;
  margin-top: -10%;
  margin-left: -20px;
  flex-shrink: 0; /* Prevent the fixed menu from shrinking */
}
/* .menu-toggle {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0;
  min-width: 60px;
  flex-shrink: 0; /* Prevent the fixed menu from shrinking 
} */


.scrollable-content {
  flex-grow: 1;  /* Allow scrollable content to take up remaining width */
  overflow-y: auto;
  padding: 20px;
  margin-top: 20px;
}

.logo-container {
  display: flex;
  justify-content: center; 
  /* margin-bottom: 60px; */
}

.logo {
  width: 75px;
}

.sidebar-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-item {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
  font-family: 'Pangram', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #828282;
  cursor: pointer;
}

.sidebar-item:hover {
  color: #2F80ED;
}

.icon {
  /* margin-right: 14px; */
  font-size: 20px;
  color: #828282;
  margin-left: 9px;
  padding-top: 9px;
  padding-bottom: 9px;
  /* padding-right: 8px; */
  
}

.icon-folder-icon {
  border-radius: 70%;
  font-size: 20px;
  padding: 9px;
  background: linear-gradient(to right, #0D25FF, #FF9DDE);
  color: #FFFFFF;
}


.sidebar-item:hover .icon {
  border-radius: 70%;
  margin-right: 6px; 
  font-size: 20px;
  color: #FFFFFF;
  background: linear-gradient(to right, #0D25FF, #FF9DDE);
  padding: 8px;       /* Adjust padding to control background size */
}
 .search-container2 {
  margin-left: 10px;
  margin-top: 35PX;
  padding: 5px;
 }
.search-container2:hover {
  cursor: pointer;
  color: #2F80ED;

}
.avatar2 {
  margin-right: 14px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 9px;
}
.avatar2:hover {
  cursor: pointer;
}
.chevron-icon {
  margin-left: auto;
  transition: transform 0.3s ease;
}

.sidebar-item.open .chevron-icon {
  transform: rotate(180deg);
}

.submenu {
  list-style: none;
  padding-left: 20px;
  margin-top: 10px;
}

.submenu-item {
  font-size: 14px;
  line-height: 20px;
  color: #828282;
  cursor: pointer;
  margin-bottom: 8px;
}

.submenu-item:hover {
  color: #2F80ED;
}

.search-container {
  display: flex;
  align-items: center;
  border: 1px solid #E0E0E0;
  border-radius: 20px;
  padding: 8px 12px;
  margin-bottom: 20px;
  margin-top: 44px;
  width: 180px;
}

.search-icon {
  color: #828282;
  margin-right: 8px;
}

.search-input {
  border: none;
  outline: none;
  flex-grow: 1;
  background-color: transparent;
}

.search-input::placeholder {
  color: #828282;
}

.tabs {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.tab-button {
  border: none;
  background-color: transparent;
  padding: 8px 12px;
  border-radius: 8px;
  font-weight: 500;
  color: #828282;
  cursor: pointer;
}

.tab-button.active {
  background-color:black;
  color: #F2F2F2;
}

.project-list {
  margin-bottom: 30px;
}

.project-item {
  display: flex;
  align-items: center;
  padding: 12px 0;
  margin-bottom: 8px;
  border-radius: 30%;
}

.project-item:hover {
  background-color: #EDEDED;
  cursor: pointer;
}

.project-icon-container {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
}

.project-icon {
  color: #828282;
  font-size: 16px;
}
.desktop-icon {
  color: black;
  font-size: 20px;
}

.project-name {
  font-weight: 500;
  color: #333;
}

.project-info {
  font-size: 12px;
  color: #828282;
}

.project-details {
  flex-grow: 1;
}

.chevron-right-icon {
  color: #828282;
  margin-left: 12px;
}

.bottom-navigation {
  margin-top: auto;
}

.sidebar-item .icon {
  color: #828282;
}

.folder-icon {
  color: #F2C94C;
}

.avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.text{
  font-size: 20px;
  color: black;
  margin-top: -52px;
}

.search-avatar-container{
  margin-top: auto;
}